<template>
  <div>
    <el-card class="box-card">
      <!-- 搜索框 -->
      <el-input
        placeholder="请输入内容"
        v-model="params.query"
        class="input-with-select"
      >
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>

      <!-- 搜索框 -->
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column
          label="#"
          width="60"
          type="index"
          sortable
        ></el-table-column>
        <el-table-column label="订单编号" prop="order_number">
        </el-table-column>
        <el-table-column label="订单价格" prop="order_price"> </el-table-column>
        <el-table-column label="是否付款" prop="order_pay">
          <template slot-scope="scoped">
            <el-button
              type="primary"
              size="mini"
              v-if="scoped.row.order_pay == 1"
              >已付款</el-button
            >
            <el-button
              type="danger"
              size="mini"
              v-else-if="scoped.row.order_pay == 0"
              >未付款</el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="是否发货" prop="is_send"> </el-table-column>
        <el-table-column label="下单时间">
          <template slot-scope="socpe">
            <div>
              {{ socpe.row.create_time | format() }}
            </div>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scoped">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              :a="scoped.row"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location-information"
              size="mini"
              @click="logistics"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.pagesize"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 分页 -->
    </el-card>
    <!-- 物流对话框 -->
    <el-dialog title="物流进度" :visible.sync="dialogVisible" width="50%">
      <div class="block">
        <div class="radio">
          <el-radio-group v-model="reverse"> </el-radio-group>
        </div>

        <el-timeline :reverse="reverse">
          <el-timeline-item
            :icon="activity.icon"
            :color="activity.color"
            :size="activity.size"
            v-for="(activity, index) in activities"
            :key="index"
            :timestamp="activity.timestamp"
          >
            <div>
              <p style="margin: 10px">{{ activity.context }}</p>
              <span style="color: #ccc">{{ activity.time }}</span>
            </div>
          </el-timeline-item>
        </el-timeline>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      params: {
        query: '',
        pagenum: 1,
        pagesize: 4
      },
      tableData: [],
      total: 1,
      time: new Date(),
      dialogVisible: false,
      reverse: false,
      activities: []
    }
  },
  created () {
    this.getordersdata()
  },
  methods: {
    async getordersdata () {
      const res = await this.$http.get('orders', { params: this.params })
      this.tableData = res.data.data.goods
      this.total = res.data.data.total
      // console.log(res, 66666)
    },
    // 分页
    handleSizeChange (val) {
      this.params.pagesize = val
      this.getordersdata()
    },
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.getordersdata()
    },
    async getlogisticsdata () {
      const res = await this.$http.get('/kuaidi/1106975712662')
      this.activities = res.data.data
      this.activities[0].color = '#0bbd87'
      this.activities[0].icon = 'el-icon-location-information'
      this.activities[0].size = 'large'
    },
    // 物流
    logistics () {
      this.dialogVisible = true
      this.getlogisticsdata()
    }
  }
}
</script>
<style lang="less" scoped>
.el-input {
  width: 40%;
  margin-right: 20px;
}
.el-table {
  margin-top: 20px;
}
.el-pagination {
  margin-top: 20px;
}
</style>
